.item{
    width: 280px;

}
.item img{
    width: 280px;
}
.item p{
    margin: 6px 0px;
}
.item-prices{
    display: flex;
    gap: 10px;
}
.item-price-new{
    color: #374151;
    font-size: 18px;
    font-weight: 610px;
}
.item-price-old{
    color: #8c8c8c;
    font-size: 18px;
    font-weight: 555;
    text-decoration: line-through;
}

.item:hover{
    transform: scale(1.05);
    transition: 0.6s;
}