.product-display{
    display: flex;
    margin: 10px 20px;
}
.productdisplay-left{
    display: flex;
    gap: 17px;
}
.productdisplay-img-list{
    display: flex;
    flex-direction: column;
    gap: 16px;
}
.productdisplay-img-list img{
    height: 153px;  
}
.product-display-main-img{
    width: 580px;
    height: 800px;
}
.productdisplay-right{
    margin-left: 30px;
    display: flex;
    flex-direction: column;
}
.productdisplay-right h1{
    color: #3d3d3d;
    font-size: 26px;
    font-weight:700;
}
.productdisplay-right-star{
    display: flex;
    align-items: center;
    margin-top: 13px;
    gap: 5px;
    color: #1c1c1c;
    font-size: 17px;
}
.productdisplay-right-prices{
    display: flex;
    margin: 20px 0px;
    gap: 30px;
    font-size: 24px;
    font-weight: 700;
}
.productdisplay-right-price-old{
    color: #818181;
    text-decoration:line-through;
}
.productdisplay-right-price-new{
    color: red;
}
.productdisplay-right-size h1{
   margin-top: 10px;
   color: #656565;
   font-size: 20px;
   font-weight: 600;
}
.productdisplay-right-sizes{
    display: flex;
    margin: 20px 0px;
    gap: 20px;
}
.productdisplay-right-sizes div{
    padding: 18px 24px;
    background-color: #fbfbfb;
    border: 1px solid #ebebeb;
    border-radius: 3px;
    cursor: pointer;
}
.productdisplay-right button{
    padding: 20px 40px;
    width: 200px;
    font-size: 16px;
    font-weight: 600;
    color: white;
    background: red;
    margin-bottom: 40px;
    border: none;
    outline: none;
    cursor: pointer;
}
.productdisplay-right-category{
    margin-top: 10px;

}
.productdisplay-right-category span{
    font-weight: 700;
}

@media screen and (max-width:430px){
    .product-display-main-img{
        width: 180px;
        height: 200px;
    }
}
@media screen and (max-width:920px){
    .product-display{
        display: flex;
        flex-wrap: wrap;
        margin: 10px 20px;
    }
}